.dev-icons {
  padding-left: 0;
  list-style: none;
  font-size: 3rem;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
}

.software-skill-inline {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
}

.software-skill-inline > i {
  color: #868e96;
  font-size: 40px;
  width: 60px;
}

.software-skill-inline > i:hover {
  color: #645beb;
}
.software-skill-inline > p {
  color: #868e96;
  font-size: 1rem;
}
.software-skill-inline > i:hover ~ p {
  color: #645beb;
}

@media (max-width: 768px) {
  .software-skill-inline > i {
    color: #868e96;
    font-size: 30px;
    width: 60px;
  }
}
